@use '../base/variables' as var;
@use '../base/mixins' as mix;
@use 'sass:color';

.btn-1{
    background-clip: padding-box;
    background-color: var.$secundario;
    border: 1px solid transparent;
    border-radius: .25rem;
    box-shadow: var.$difuminado_botones;
    box-sizing: border-box;
    color: var.$blanco;
    display: block;
    margin: 1rem auto 0 auto;
    min-height: 3rem;
    padding: calc(.875rem - 1px) calc(1.5rem - 1px);
    position: relative;
    transition: all 250ms;
    user-select: none;
    width: fit-content;
}

.btn-1:hover,
.btn-1:focus {
  background-color: #fb8332;
  box-shadow: var.$difuminado;
}

.btn-1:hover {
  transform: translateY(-1px);
}

.btn-1 {
  background-color: #c85000;
  box-shadow: rgba(0, 0, 0, .06) 0 2px 4px;
  transform: translateY(0);
}


.btn-volver {
  font-size: 1.2rem;
    margin: 1rem 0 2rem 2rem; // separa del contenido y lo alinea a la izquierda
    display: inline-block;
}

.btn-opcion {
  background-color: var.$secundario;
  color: #fff;
  text-decoration: none;
  padding: 1.5rem 1rem;
  border-radius: 10px;
  text-align: center;
  font-size: 1.1rem;
  font-weight: 600;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    background-color: color.scale(var.$secundario, $lightness: -10%);
    transform: translateY(-3px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  }
}

.btn-opcion.verde {
  background-color: #28a745;

  &:hover {
    background-color: color.scale(#28a745, $lightness: -10%);
  }
}

.btn-opcion.rojo {
  background-color: #dc3545;

  &:hover {
    background-color: color.scale(#dc3545, $lightness: -10%);
  }
}

.button-input {
  background-image: linear-gradient(#42A1EC, #0070C9);
  border: 1px solid #0077CC;
  border-radius: 4px;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  direction: ltr;
  display: block;
  font-family: "SF Pro Text","SF Pro Icons","AOS Icons","Helvetica Neue",Helvetica,Arial,sans-serif;
  font-size: 15px;
  font-weight: 400;
  min-width: 30px;
  overflow: visible;
  padding: 4px 15px;
  text-align: center;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
}

.button-input:disabled {
  cursor: default;
  opacity: .3;
}

.button-input:hover {
  background-image: linear-gradient(#51A9EE, #147BCD);
  border-color: #1482D0;
  text-decoration: none;
}

.button-input:active {
  background-image: linear-gradient(#3D94D9, #0067B9);
  border-color: #006DBC;
  outline: none;
}

.button-input:focus {
  box-shadow: rgba(131, 192, 253, 0.5) 0 0 0 3px;
  outline: none;
}

.button-enviar {
    background: #28a745;
    color: white;
    padding: 10px 15px;
    border: none;
    cursor: pointer;
}
.button-enviar:hover {
    background: #218838;
}

.boton-responder {
    background-color: #007bff;
    color: white;
    padding: 6px 10px;
    text-decoration: none;
    border-radius: 4px;
}

.boton-responder:hover {
  background-color: #0056b3;
}

.acciones a {
  margin-right: 4px;
  text-decoration: none;
  padding: 6px 10px;
  border-radius: 4px;
  color: white;
}

.editar { 
  background-color: #007bff;
  margin-bottom: 0.5rem;
}

.eliminar { background-color: #dc3545; }

.crear-btn {
  display: inline-block;
  margin-bottom: 20px;
  padding: 10px 16px;
  background-color: #28a745;
  color: white;
  text-decoration: none;
  border-radius: 4px;
}

.volver {
  background: #343a40;
  color: #fff;
  padding: 6px 12px;
  text-decoration: none;
  border-radius: 4px;
  font-size: 1.5rem;
}

.volver:hover {
  background-color: #000;
}

.toggle {
  background-color: #17a2b8;
}

.activo {
  color: green;
  font-weight: bold;
}

.inactivo {
  color: red;
  font-weight: bold;
}

.button-18 {
  align-items: center;
  background-color: #0A66C2;
  border: 0;
  border-radius: 100px;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  display: inline-flex;
  font-family: -apple-system, system-ui, system-ui, "Segoe UI", Roboto, "Helvetica Neue", "Fira Sans", Ubuntu, Oxygen, "Oxygen Sans", Cantarell, "Droid Sans", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Lucida Grande", Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 600;
  justify-content: center;
  line-height: 20px;
  max-width: 480px;
  min-height: 40px;
  min-width: 0px;
  overflow: hidden;
  padding: 0px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
  touch-action: manipulation;
  transition: background-color 0.167s cubic-bezier(0.4, 0, 0.2, 1) 0s, box-shadow 0.167s cubic-bezier(0.4, 0, 0.2, 1) 0s, color 0.167s cubic-bezier(0.4, 0, 0.2, 1) 0s;
  user-select: none;
  -webkit-user-select: none;
  vertical-align: middle;
}

.button-18:hover,
.button-18:focus { 
  background-color: #16437E;
  color: #ffffff;
}

.button-18:active {
  background: #09223b;
  color: rgb(255, 255, 255, .7);
}

.button-18:disabled { 
  cursor: not-allowed;
  background: rgba(0, 0, 0, .08);
  color: rgba(0, 0, 0, .3);
}